<template>
  <div>
    <span
      style="  display: inline-block;
  width: 100%;
  text-align: center;
  font-size:2em;
  margin:0;"
    >通过文件导入</span>
    <el-upload
      class="upload-demo item-center"
      drag
      :action="url"
      accept=".xls, .xlsx"
      :on-success="success"
      :on-error="error"
      :on-progress="progress"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
      <div class="el-upload__tip" slot="tip" v-loading.fullscreen.lock="isLoading">只能上传excel文件</div>
    </el-upload>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "ImportTeacher2",
  data: function() {
    return {
      url: this.$store.state.prefixUrl + "/teacher/import_manager2",
      isLoading: false
    };
  },
  methods: {
    success: function(res, file, file_list) {
      this.$message({
        message: "导入成功!",
        type: "success"
      });
    },
    error: function(err, file, file_list) {
      this.$message({
        message: err.response.data["status"],
        type: "error"
      });
    },
    progress: function(e, file, file_list) {
      this.isLoading = true;
    }
  }
};
</script>

<style scoped>
.input-div {
  display: inline-block;
  padding-left: 5vw;
  margin-top: 3vh;
  padding-right: 4%;
  width: 60%;
}

.el-input {
  width: 50%;
  margin-right: 2vw;
}

span {
  display: inline-block;
  width: 8em;
  margin-right: 1em;
}

.mybutton {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2vh;
}

.item-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
}
</style>
