<template>
  <div class="ChooseCampus">
    <div class="input-div">
      <span>学院</span>
      <el-select v-model="campus" placeholder="请选择" @change="campus_select">
        <el-option v-for="item in campus_options" :key="item" :value="item"></el-option>
      </el-select>
    </div>

    <div class="input-div">
      <span>具体</span>
      <el-select v-model="detail" placeholder="请选择">
        <el-option v-for="item in detail_options" :key="item" :value="item"></el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChooseCampus",
  data: function() {
    return {
      campus: "全部",
      detail: "全部",
      campus_options: [
        "软件学院",
        "物理学院",
        "环境工程与工程学院",
        "药学院",
        "生物学院",
        "全部"
      ],
      detail_options: ["全部"],
      software_options: ["本科生", "研究生", "全部"],
      biology_options: ["泰达籍", "生科籍", "全部"],
      physics_options: ["泰达籍", "物理籍", "临时", "全部"],
      environment_options: ["全部"],
      medicine_options: ["全部"]
    };
  },
  methods: {
    campus_select: function(e) {
      this.$data.detail = "全部";
      switch (e) {
        case "软件学院": {
          this.$data.detail_options = this.$data.software_options;
          break;
        }
        case "物理学院": {
          this.$data.detail_options = this.$data.physics_options;
          break;
        }
        case "环境工程与工程学院": {
          this.$data.detail_options = this.$data.environment_options;
          break;
        }
        case "药学院": {
          this.$data.detail_options = this.$data.medicine_options;
          break;
        }
        case "生物学院": {
          this.$data.detail_options = this.$data.biology_options;
          break;
        }
        default: {
          this.$data.detail_options = ["全部"];
        }
      }
    }
  }
};
</script>

<style scoped>
span {
  display: inline-block;
  width: 3em;
  margin-right: 1em;
}

.ChooseCampus {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.input-div {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-right: 5%;
}
</style>
