<template>
  <div id="App">
    <el-container>
      <el-header>
        <el-row>
          <!--log-->
          <el-col :span="2" class="font-center">
            <i class="el-icon-menu" style="color: white;"></i>
          </el-col>
          <!--主题-->
          <el-col :span="18">
            <span class="topic">
              <i class="el-icon-s-promotion" style="color: white;"></i>
              <font color="white">宿舍管理系统</font>
            </span>
          </el-col>
          <!--退出-->
          <el-col :span="4" class="logout">
            <span style="display:block;margin-right:1vw;color: white;">欢迎，{{name}}</span>
            <el-button @click="logout" round icon="el-icon-switch-button"></el-button>
          </el-col>
        </el-row>
      </el-header>

      <el-container>
        <!--菜单-->

        <el-aside>
          <div id="sidemenu"></div>
        </el-aside>
        <el-main>
          <router-view name="body"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>


<script>
import AsideMenuStudent from "./components/student/AsideMenu";
import AsideMenuTeacher from "./components/teacher/AsideMenu";
import store from "./store";
import Vue from "vue";
import router from "./router/router";
import VueRouter from "vue-router";
import axios from "axios";

Vue.use(axios);
// axios.defaults.baseURL = "http://127.0.0.1:8000";

Vue.use(VueRouter);
export default {
  name: "Main",
  store,
  router,
  computed: {
    identity() {
      return this.$store.state.identity;
    }
  },
  data: function() {
    return {
      name: this.$cookie.get("name")
    };
  },
  methods: {
    logout: function() {
      this.$cookie.delete("name");
      this.$cookie.delete("type");
      this.$cookie.delete("account");
      this.$cookie.delete("level");
      this.$router.go({ name: "/" });
    }
  },
  created: function() {
    this.$router.push(this.$router.path);
  },
  mounted: function() {
    //检查是学生还是老师
    if (this.identity == "teacher") {
      new Vue({
        router,
        render: h => h(AsideMenuTeacher)
      }).$mount("#sidemenu");
    } else if (this.identity == "student") {
      new Vue({
        router,
        render: h => h(AsideMenuStudent)
      }).$mount("#sidemenu");
    }
  }
};
</script>


<style scoped>
#App {
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  overflow: hidden;
}

.el-header {
  background: rgb(126, 12, 110);
  height: 10vh !important;
  width: 100vw;
  box-shadow: 5px rgb(126, 12, 110);
}

.el-aside {
  background: rgb(253, 249, 249);
  height: 90vh;
  width: 15vw !important;
}

.el-main {
  background: rgb(253, 249, 249);
  height: 90vh;
  width: 85vw !important;
}

.font-center {
  font-size: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.el-row,
.el-col {
  height: 100%;
}

.topic {
  font-size: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.logout {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.body {
  width: 100%;
  height: 100%;
}
</style>
