<template>
  <el-table
    :data="tableData"
    border
    style="width: 100%"
    height="70vh"
    :stripe="true"
    :highlight-current-row="true"
  >
    <el-table-column prop="date" label="日期" min-width="6"></el-table-column>
    <el-table-column prop="build" label="宿舍楼" min-width="3"></el-table-column>
    <el-table-column prop="door" label="门洞号" min-width="3"></el-table-column>
    <el-table-column prop="room" label="宿舍号" min-width="4"></el-table-column>
    <el-table-column prop="singleRoom" label="单间号" min-width="4"></el-table-column>
    <el-table-column prop="result" label="结果" min-width="6"></el-table-column>
    <el-table-column fixed="right" label="操作" min-width="4">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row)" type="text" size="small">查看备注</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
/* eslint-disable */
import axios from "axios";
export default {
  name: "HygieneHistoryTable",
  data: function() {
    return {
      tableData: []
    };
  },
  methods: {
    updateData: function(start_date, end_date, campus, detail, parentThat) {
      let that = this;
      axios
        .post(this.$store.state.prefixUrl + "/teacher/HygieneHistory", {
          start_date: start_date,
          end_date: end_date,
          campus: campus,
          detail: detail
        })
        .then(function(res) {
          console.log(res);
          parentThat.$data.isLoading = false;
          that.$data.tableData = res.data.data;
          that.$message({
            message: "查询成功!",
            type: "success"
          });
        })
        .catch(function(error) {
          console.log(error);
          parentThat.$data.isLoading = false;
          that.$message({
            message: error.response.data["status"]
              ? error.response.data["status"]
              : "网络错误, 请稍后尝试!",
            type: "error"
          });
        });
    },
    handleClick: function(data) {
      this.$alert(data.comment, "备注", {
        confirmButtonText: "确定"
      });
    }
  }
};
</script>

