
<template>
  <el-table :data="tableData" border>
    <el-table-column prop="build" min-width="1" label="宿舍楼"></el-table-column>
    <el-table-column prop="door" min-width="1" label="门洞号"></el-table-column>
    <el-table-column prop="room" min-width="1" label="宿舍号"></el-table-column>
    <el-table-column prop="singleRoom" min-width="1" label="单间号"></el-table-column>
    <el-table-column fixed="right" label="操作" min-width="1">
      <template slot-scope="scope">
        <el-button @click="update(scope);" type="text" size="small">查看宿舍成员</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
/* eslint-disable */
export default {
  name: "DormInfos",
  data: function() {
    return {
      tableData: [],
      innerDrawer: false,
      data: null
    };
  },
  methods: {
    update: function(scope) {
      this.data = scope.row;
      this.innerDrawer = true;
      this.$emit("update");
    }
  }
};
</script>

