<template>
  <div>
    <div class="input-div">
      <span>学号/教职工号</span>
      <el-input v-model="id" placeholder="请输入学号/教职工号"></el-input>
      <el-button type="success" round @click="reset">重置密码</el-button>
    </div>

    <hr style="margin-top:10vh;color:gray;width:100%;margin-right:0;padding:0;" />

    <span
      style="  display: inline-block;
  width: 100%;
  text-align: center;
  font-size:2em;
  margin:0;"
    >通过文件导入</span>
    <el-upload
      class="upload-demo item-center"
      drag
      :action="url"
      accept=".xls, .xlsx"
      :on-success="success"
      :on-error="error"
      :on-progress="progress"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
      <div class="el-upload__tip" slot="tip" v-loading.fullscreen.lock="isLoading">只能上传excel文件</div>
    </el-upload>
  </div>
</template>

<script>
/* eslint-disable */
import Axios from "axios";
export default {
  name: "ResetAccount",
  data: function() {
    return {
      url: this.$store.state.prefixUrl + "/teacher/reset_account",
      isLoading: false
    };
  },
  methods: {
    reset: function() {
      Axios.post(this.$store.state.prefixUrl + "/teacher/reset_account")
        .then(function(res) {
          that.$message({
            message: "删除成功!",
            type: "success"
          });
        })
        .catch(function(error) {
          that.$message({
            message: error.response.data["status"],
            type: "error"
          });
        });
    },
    success: function(res, file, file_list) {
      this.$message({
        message: "导入成功!",
        type: "success"
      });
    },
    error: function(err, file, file_list) {
      this.$message({
        message: err.response.data["status"],
        type: "error"
      });
    },
    progress: function(e, file, file_list) {
      this.isLoading = true;
    }
  }
};
</script>

<style scoped>
.input-div {
  display: inline-block;
  padding-left: 5vw;
  margin-top: 3vh;
  padding-right: 4%;
  width: 60%;
}

.el-input {
  width: 50%;
  margin-right: 2vw;
}

span {
  display: inline-block;
  width: 8em;
  margin-right: 1em;
}

.mybutton {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2vh;
}

.item-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
}
</style>
