<template>
  <div class="SearchWithTarget">
    <div>
      <ChooseRoom ref="room"></ChooseRoom>
    </div>

    <div class="align">
      <ChooseCampus ref="campus"></ChooseCampus>
    </div>

    <div class="submit">
      <el-button @click="drawer=true;search();" type="primary" icon="el-icon-search">搜索</el-button>
      <el-drawer title="宿舍信息" :visible.sync="drawer" size="60%" direction="ttb">
        <div>
          <DormInfos ref="DormInfos" @update="update"></DormInfos>
          <!-- <el-button @click="innerDrawer = true;">打开里面的!</el-button> -->
          <el-drawer
            title="宿舍成员信息"
            :append-to-body="true"
            :before-close="handleClose"
            :visible.sync="innerDrawer"
          >
            <MemberInfoOfDorm ref="MemberInfoOfDorm" @update_member="update_member"></MemberInfoOfDorm>
          </el-drawer>
        </div>
      </el-drawer>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ChooseCampus from "../../plugin/ChooseCampus";
import ChooseRoom from "../../plugin/ChooseRoom";
import DormInfos from "../../plugin/DormInfos";
import MemberInfoOfDorm from "../../plugin/MemberInfoOfDorm";
import Axios from "axios";

export default {
  name: "SearchWithTarget",
  components: {
    ChooseCampus,
    ChooseRoom,
    DormInfos,
    MemberInfoOfDorm
  },
  methods: {
    handleClose: function(done) {
      done();
    },
    search: function() {
      const build = this.$refs.room.$data.build;
      const door = this.$refs.room.$data.door;
      const singleRoom = this.$refs.room.$data.singleRoom;
      const room = this.$refs.room.$data.room;

      const campus = this.$refs.campus.$data.campus;
      const detail = this.$refs.campus.$data.detail;

      var that = this;
      Axios.post(this.$store.state.prefixUrl + "/teacher/search_with_target", {
        build: build,
        door: door,
        singleRoom: singleRoom,
        room: room,
        campus: campus,
        detail: detail
      })
        .then(function(res) {
          that.$refs.DormInfos.$data.tableData = res.data;
          that.$message({
            message: "查询成功",
            type: "success"
          });
        })
        .catch(function(error) {
          that.$message({
            message: error.response.data,
            type: "error"
          });
        });
    },
    update: function() {
      var that = this;
      this.innerDrawer = this.$refs.DormInfos.$data.innerDrawer;
      this.row = this.$refs.DormInfos.$data.data;
    },
    update_member: function() {
      this.$refs.MemberInfoOfDorm.$data.row = this.row;
    }
  },
  data: function() {
    return {
      drawer: false,
      innerDrawer: false,
      row: null
    };
  }
};
</script>

<style scoped>
.align {
  margin-top: 2vh;
  margin-left: 5vw;
}

.submit {
  margin-top: 2vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
</style>
