<template>
  <div class="More">
    <el-carousel type="card" trigger="click" height="40vh" @change="change_card">
      <el-carousel-item v-for="item in card_list" :key="item">
        <h3 style="text-align: center;">{{ item }}</h3>

        <!--注意判断item的值,否则会所有卡片显示一样的值-->
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: "More",
  data: function() {
    return {
      card_list: ["", "", "", "..."],
      is_contact_author: false
    };
  },
  methods: {
    change_card: function(e) {
      switch (e) {
        case 0: {
          this.$data.is_contact_author = false;
          break;
        }
        case 1: {
          this.$data.is_contact_author = false;
          break;
        }
        case 2: {
          this.$data.is_contact_author = true;
          break;
        }
        default: {
          this.$data.is_contact_author = false;
          break;
        }
      }
    }
  }
};
</script>

<style scoped>
.More {
  width: 50vw;
  margin: 5vh auto auto;
  height: 40vh;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>
