<template>
  <div id="App">
    <div id="login-button-student" v-on:click="stu_login">
      <img src="./assets/login/stu_login.png" />
    </div>

    <div id="login-button-teacher" v-on:click="tea_login">
      <img src="./assets/login/tea_login.png" />
    </div>
    <div id="container">
      <h1 v-if="identity=='student'">学生登录</h1>
      <h1 v-if="identity=='teacher'">管理员登录</h1>
      <span class="close-btn">
        <img src="./assets/login/close_btn.png" />
      </span>

      <form>
        <input v-model="account" type="text" name="account" placeholder="账号" />
        <input v-model="pwd" type="password" name="password" placeholder="密码" />
        <input type="hidden" v-bind:value="identity" />
        <a v-on:click="login" v-loading.fullscreen.lock="isLoading">登录</a>
        <div id="remember-container">
          <input
            v-model="isRemember"
            type="checkbox"
            id="rember"
            class="checkbox"
            checked="checked"
          />
          <span id="remember">记住密码</span>
          <span id="forgotten">忘记密码?</span>
        </div>
      </form>
    </div>

    <div id="forgotten-container">
      <h1>找回密码</h1>
      <span class="close-btn">
        <img src="./assets/login/close_btn.png" />
      </span>

      <form>
        <input type="email" name="email" placeholder="学校邮箱(该功能暂未开发)" />
        <a href="#" class="orange-btn">获取密码</a>
      </form>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import TweenMax from "gsap";
import Sine from "gsap";
import $ from "jquery";
import store from "./store";
import Vue from "vue";
import Main from "./Main.vue";
import axios from "axios";
import App from "./App";
export default {
  store,
  name: "Login",
  computed: {
    identity() {
      return this.$store.state.identity;
    }
  },
  data: function() {
    return {
      isLoading: false,
      account: "",
      pwd: "",
      isRemember: false
    };
  },
  created: function() {
    if (this.$router.path != "/") {
      this.$router.push("/");
    }
  },
  methods: {
    stu_login: function() {
      this.$store.state.identity = "student";

      if (this.$cookie.get("last_password_stu"))
        this.$data.pwd = this.$cookie.get("last_password_stu");
      else this.$data.pwd = "";
      if (this.$cookie.get("last_account_stu"))
        this.$data.account = this.$cookie.get("last_account_stu");
      else this.$data.account = "";
      if (this.$cookie.get("last_isRemember_stu"))
        this.$data.isRemember = this.$cookie.get("last_isRemember_stu");
      else this.$data.isRemember = false;
      if (this.$cookie.get("name") && this.$cookie.get("type") == "student") {
        this.$data.isLoading = true;

        let that = this;

        setTimeout(() => {
          that.$message({
            message: "检测到登录记录, 为您自动登录中...",
            type: "success",
            onClose: function() {}
          });
          new Vue({
            render: h => h(Main),
            mounted: function() {
              that.$data.isLoading = false;
            }
          }).$mount("#App");
        }, 300);
      }
    },
    tea_login: function() {
      this.$store.state.identity = "teacher";
      if (this.$cookie.get("last_password_tea"))
        this.$data.pwd = this.$cookie.get("last_password_tea");
      else this.$data.pwd = "";
      if (this.$cookie.get("last_account_tea"))
        this.$data.account = this.$cookie.get("last_account_tea");
      else this.$data.account = "";
      if (this.$cookie.get("last_isRemember_tea"))
        this.$data.isRemember = this.$cookie.get("last_isRemember_tea");
      else this.$data.isRemember = false;

      if (this.$cookie.get("name") && this.$cookie.get("type") == "teacher") {
        this.$data.isLoading = true;

        let that = this;

        setTimeout(() => {
          that.$message({
            message: "检测到登录记录, 为您自动登录中...",
            type: "success",
            onClose: function() {}
          });
          new Vue({
            render: h => h(Main),
            mounted: function() {
              that.$data.isLoading = false;
            }
          }).$mount("#App");
        }, 300);
      }
    },
    login: function() {
      // remember the password
      if (this.$data.isRemember == true) {
        if (this.$store.state.identity == "student") {
          this.$cookie.set("last_account_stu", this.$data.account, 30);
          this.$cookie.set("last_password_stu", this.$data.pwd, 30);
          this.$cookie.set("last_isRemember_stu", this.$data.isRemember, 30);
        } else if (this.$store.state.identity == "teacher") {
          this.$cookie.set("last_account_tea", this.$data.account, 30);
          this.$cookie.set("last_password_tea", this.$data.pwd, 30);
          this.$cookie.set("last_isRemember_tea", this.$data.isRemember, 30);
        }
      } else {
        if (this.$store.state.identity == "student") {
          this.$cookie.delete("last_account_stu");
          this.$cookie.delete("last_password_stu");
          this.$cookie.delete("last_isRemember_stu");
        } else if (this.$store.state.identity == "teacher") {
          this.$cookie.delete("last_account_tea");
          this.$cookie.delete("last_password_tea");
          this.$cookie.delete("last_isRemember_tea");
        }
      }

      let that = this;
      this.$data.isLoading = true;
      axios
        .post(that.$store.state.prefixUrl + "/login", {
          account: that.$data.account,
          pwd: that.$data.pwd,
          type: that.$store.state.identity
        })
        .then(function(res) {
          that.$cookie.set("name", res.data.name);
          that.$cookie.set("type", that.$store.state.identity);
          that.$message({
            message: "登陆成功, 正在跳转",
            type: "success"
          });
          setTimeout(function() {
            that.$data.isLoading = false;
            new Vue({
              render: h => h(Main)
            }).$mount("#App");
          }, 1000);
        })
        .catch(function(error) {
          console.log(error.response);
          that.$data.isLoading = false;
          that.$message({
            message: error.response.data["status"]
              ? error.response.data["status"]
              : "网络错误, 请稍后重试!",
            type: "error"
          });
          setTimeout(function() {
            that.$data.isLoading = false;
            that.$router.go(0);
          }, 1000);
        })
        .catch(function(error) {
          that.$message({
            message: "网络错误, 请重试!",
            tyep: "error"
          });
        });
    }
  },
  mounted: function() {},
  beforeMount: function() {}
};

$(function() {
  $("#login-button-student").click(function() {
    $("#login-button-teacher").fadeOut("slow");
    $("#login-button-student").fadeOut("slow", function() {
      $("#container").fadeIn();
      TweenMax.from("#container", 0.4, { scale: 0, ease: Sine.easeInOut });
      TweenMax.to("#container", 0.4, { scale: 1, ease: Sine.easeInOut });
    });
  });

  $("#login-button-teacher").click(function() {
    $("#login-button-student").fadeOut("slow");
    $("#login-button-teacher").fadeOut("slow", function() {
      $("#container").fadeIn();
      TweenMax.from("#container", 0.4, { scale: 0, ease: Sine.easeInOut });
      TweenMax.to("#container", 0.4, { scale: 1, ease: Sine.easeInOut });
    });
  });

  $(".close-btn").click(function() {
    TweenMax.from("#container", 0.4, { scale: 1, ease: Sine.easeInOut });
    TweenMax.to("#container", 0.4, {
      left: "0px",
      scale: 0,
      ease: Sine.easeInOut
    });
    $("#container, #forgotten-container").fadeOut(300, function() {
      $("#login-button-student").fadeIn(300);
      $("#login-button-teacher").fadeIn(300);
    });
  });

  /* Forgotten Password */
  $("#forgotten").click(function() {
    $("#container").fadeOut(function() {
      $("#forgotten-container").fadeIn();
    });
  });
});
</script>


<style scoped>
#App {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: url(./assets/login/background.jpg) center no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  overflow: hidden;
}

img {
  display: block;
  margin: auto;
  width: 100%;
  height: auto;
}

#login-button-student {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 30px;
  margin: auto auto auto 35%;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: rgba(3, 3, 3, 0.8);
  overflow: hidden;
  opacity: 0.4;
  box-shadow: 10px 10px 30px #000;
}

#login-button-teacher {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 30px;
  margin: auto 35% auto auto;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: rgba(3, 3, 3, 0.8);
  overflow: hidden;
  opacity: 0.4;
  box-shadow: 10px 10px 30px #000;
}

#login-button-teacher:hover,
#login-button-student:hover {
  transform: scale(0.9, 0.9);
  transition: all 1s;
}

/* Login container */
#container {
  border-radius: 24%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 260px;
  height: 260px;
  border-radius: 5px;
  background: rgba(3, 3, 3, 0.25);
  box-shadow: 1px 1px 50px #000;
  display: none;
}

.close-btn {
  position: absolute;
  cursor: pointer;
  font-family: "Open Sans Condensed", sans-serif;
  line-height: 18px;
  top: 3px;
  right: 3px;
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 10px;
  opacity: 0.2;
  -webkit-transition: all 2s ease-in-out;
  -moz-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.close-btn:hover {
  opacity: 0.5;
}

/* Heading */
h1 {
  font-family: "Open Sans Condensed", sans-serif;
  position: relative;
  margin-top: 0px;
  text-align: center;
  font-size: 40px;
  color: #ddd;
  text-shadow: 3px 3px 10px #000;
}

/* Inputs */
a,
input {
  font-family: "Open Sans Condensed", sans-serif;
  text-decoration: none;
  position: relative;
  width: 80%;
  display: block;
  margin: 9px auto;
  font-size: 17px;
  color: #fff;
  padding: 8px;
  border-radius: 6px;
  border: none;
  background: rgba(3, 3, 3, 0.1);
  -webkit-transition: all 2s ease-in-out;
  -moz-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

input:focus {
  outline: none;
  box-shadow: 3px 3px 10px #333;
  background: rgba(3, 3, 3, 0.18);
}

/* Placeholders */
::-webkit-input-placeholder {
  color: #ddd;
}
:-moz-placeholder {
  /* Firefox 18- */
  color: red;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: red;
}
:-ms-input-placeholder {
  color: #333;
}

/* Link */
a {
  font-family: "Open Sans Condensed", sans-serif;
  text-align: center;
  padding: 4px 8px;
  background: rgba(107, 255, 3, 0.3);
}

a:hover {
  opacity: 0.7;
}

#remember-container {
  position: relative;
  margin: -5px 20px;
}

.checkbox {
  position: relative;
  cursor: pointer;
  -webkit-appearance: none;
  padding: 5px;
  border-radius: 4px;
  background: rgba(3, 3, 3, 0.2);
  display: inline-block;
  width: 16px;
  height: 15px;
}

.checkbox:checked:active {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.checkbox:checked {
  background: rgba(3, 3, 3, 0.4);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05),
    inset 15px 10px -12px rgba(255, 255, 255, 0.5);
  color: #fff;
}

.checkbox:checked:after {
  content: "\2714";
  font-size: 10px;
  position: absolute;
  top: 0px;
  left: 4px;
  color: #fff;
}

#remember {
  position: absolute;
  font-size: 13px;
  font-family: "Hind", sans-serif;
  color: rgba(255, 255, 255, 0.5);
  top: 7px;
  left: 20px;
}

#forgotten {
  position: absolute;
  font-size: 12px;
  font-family: "Hind", sans-serif;
  color: rgba(255, 255, 255, 0.2);
  right: 0px;
  top: 8px;
  cursor: pointer;
  -webkit-transition: all 2s ease-in-out;
  -moz-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

#forgotten:hover {
  color: rgba(255, 255, 255, 0.6);
}

#forgotten-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 260px;
  height: 180px;
  border-radius: 10px;
  background: rgba(3, 3, 3, 0.25);
  box-shadow: 1px 1px 50px #000;
  display: none;
}

.orange-btn {
  background: rgba(87, 198, 255, 0.5);
}
</style>
