import Router from 'vue-router'
import Vue from 'vue'
import Login from '../Login.vue'
import Main from "../Main.vue"

// teacher
import HygieneHistory from "../components/teacher/hygiene/HygieneHistory"
import AddHygieneHistory from "../components/teacher/hygiene/AddHygieneHistory"
import SendWarning from "../components/teacher/hygiene/SendWarning"
import ManageBed from "../components/teacher/dorm/ManageBed"
import SearchWithTarget from "../components/teacher/search/SearchWithTarget"
import DeleteAccount from "../components/teacher/account/DeleteAccount"
import ImportManager1 from "../components/teacher/account/ImportManager1"
import ImportManager2 from "../components/teacher/account/ImportManager2"
import ImportStudent from "../components/teacher/account/ImportStudent"
import ResetAccount from "../components/teacher/account/ResetAccount"
import AccountInformationManager from "../components/teacher/AccountInformationManager"

// student
import AccountInformationStudent from '../components/student/AccountInformationStudent'
import SameRoom from "../components/student/dorm/SameRoom"
import ResultOfCheck from '../components/student/hygiene/ResultOfCheck'
import WarningMessage from "../components/student/hygiene/WarningMessage"

import More from "../components/appendix/More"
Vue.use(Router)
Vue.use(HygieneHistory)


export default new Router({
    routes: [
        {
            path: "/main",
            name: "Main",
            component: Main
        },
        {
            path: "/",
            name: "Login",
            component: Login
        },
        {
            path: "/More",
            name: "More",
            components: {
                body: More
            }
        },

        // teacher
        {
            path: "/HygieneHistory",
            name: "HygieneHistory",
            components: {
                body: HygieneHistory
            }
        },
        {
            path: "/AddHygieneHistory",
            name: "AddHygieneHistory",
            components: {
                body: AddHygieneHistory
            }
        },
        {
            path: "/SendWarning",
            name: "SendWarning",
            components: {
                body: SendWarning
            }
        },
        {
            path: "/ManageBed",
            name: "ManageBed",
            components: {
                body: ManageBed
            }
        },
        {
            path: "/SearchWithTarget",
            name: "SearchWithTarget",
            components: {
                body: SearchWithTarget
            }
        },
        {
            path: "/DeleteAccount",
            name: "DeleteAccount",
            components: {
                body: DeleteAccount
            },
        },
        {
            path: "/ImportManager1",
            name: "ImportManager1",
            components: {
                body: ImportManager1
            },
        },
        {
            path: "/ImportManager2",
            name: "ImportManager2",
            components: {
                body: ImportManager2
            },
        },
        {
            path: "/ImportStudent",
            name: "ImportStudent",
            components: {
                body: ImportStudent
            },
        },
        {
            path: "/ResetAccount",
            name: "ResetAccount",
            components: {
                body: ResetAccount
            },
        },
        {
            path: "/AccountInformationManager",
            name: "AccountInformationManager",
            components: {
                body: AccountInformationManager
            }
        },

        // student
        {
            path: "/AccountInformationStudent",
            name: "AccountInformationStudent",
            components: {
                body: AccountInformationStudent
            }
        },
        {
            path: "/SameRoom",
            name: "SameRoom",
            components: {
                body: SameRoom
            },
        },
        {
            path: "/ResultOfCheck",
            name: "ResultOfCheck",
            components: {
                body: ResultOfCheck
            }
        },
        {
            path: "/WarningMessage",
            name: "WarningMessage",
            components: {
                body: WarningMessage
            }
        }

    ]
})
