<template>
  <div>
    <ChooseRoom ref="room"></ChooseRoom>

    <!--警告等级和备注-->
    <div class="center">
      <div class="inline warning">
        <span>警告</span>
        <el-select v-model="warning">
          <el-option v-for="item in warning_options" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </div>
      <div class="inline comment">
        <span>备注</span>
        <el-input v-model="comment" placeholder="请输入"></el-input>
      </div>
    </div>

    <!--提交按钮-->
    <div class="mybutton">
      <el-button @click="submit" type="success" round>发送</el-button>
    </div>

    <hr style="margin-top:10vh;color:gray;width:100%;margin-right:0;padding:0;" />

    <span
      style="  display: inline-block;
  width: 100%;
  text-align: center;
  font-size:2em;
  margin-right:0;"
    >
      通过文件导入
      <span style="color:gray">[暂未开放]</span>
    </span>
    <el-upload
      class="upload-demo item-center"
      drag
      disabled
      action="https://jsonplaceholder.typicode.com/posts/"
      accept=".xls, .xlsx"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
      <div class="el-upload__tip" slot="tip">只能上传excel文件</div>
    </el-upload>
  </div>
</template>

<script>
/* eslint-disable */
import ChooseRoom from "../../plugin/ChooseRoom";
import Axios from "axios";
export default {
  name: "SendWarning",
  components: {
    ChooseRoom
  },
  data: function() {
    return {
      warning: "一般",
      warning_options: ["轻微", "一般", "严重"],
      comment: ""
    };
  },
  methods: {
    submit: function() {
      const build = this.$refs.room.$data.build;
      const door = this.$refs.room.$data.door;
      const singleRoom = this.$refs.room.$data.singleRoom;
      const room = this.$refs.room.$data.room;
      const warning = this.$data.warning;
      const comment = this.$data.comment;
      var that = this;
      Axios.post(this.$store.state.prefixUrl + "/teacher/send_warning", {
        build: build,
        door: door,
        singleRoom: singleRoom,
        room: room,
        warning: warning,
        comment: comment
      }).then(function(res) {
        that.$message({
          message: "发送成功!",
          type: "success"
        });
      });
    }
  }
};
</script>

<style scoped>
.center {
  padding-left: 5vw;
  display: flex;
  margin-top: 2vh;
}

.inline {
  display: inline-block;
}

.warning {
  margin-right: 3vw;
}

.comment {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 40%;
  margin-right: 0;
}

span {
  width: 3em;
  margin-right: 2em;
}

.mybutton {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2vh;
}

.item-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
}
</style>
