<template>
  <div class="addHygiene">
    <ChooseRoom ref="room"></ChooseRoom>

    <div class="input-div">
      <span>结果</span>
      <el-select v-model="result" placeholder="请选择" aria-required="true">
        <el-option v-for="item in result_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div class="input-div">
      <span>备注</span>
      <el-input v-model="comment" placeholder="请输入"></el-input>
    </div>

    <div class="center-button">
      <el-button
        @click="reset"
        class="button-delete mybutton"
        type="danger"
        icon="el-icon-delete"
        title="重置"
        circle
      ></el-button>

      <el-button
        @click="submit"
        class="button-check mybutton"
        type="success"
        icon="el-icon-check"
        title="提交"
        circle
      ></el-button>
    </div>

    <hr style="margin-top:10vh;color:gray;width:100%;margin-right:0;padding:0;" />

    <span
      style="  display: inline-block;
  width: 100%;
  text-align: center;
  font-size:2em;
  margin-right:0;"
    >通过文件导入</span>
    <el-upload
      class="upload-demo item-center"
      drag
      submit="submit"
      accept=".xls, .xlsx"
      :action="upload_url"
      :on-success="upload_success"
      :on-error="upload_error"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
      <div class="el-upload__tip" slot="tip">只能上传excel文件</div>
    </el-upload>
  </div>
</template>

<script>
/* eslint-disable */
import ChooseRoom from "../../plugin/ChooseRoom";
import Axios from "axios";
export default {
  name: "AddHygieneHistory",
  components: {
    ChooseRoom
  },
  data: function() {
    return {
      result: "优秀",
      comment: "",
      result_options: ["优秀", "良好", "中等", "较差", "极差"],
      upload_url: this.$store.state.prefixUrl + "/teacher/add_HygieneHistory"
    };
  },
  methods: {
    reset: function() {
      this.$data.result = "优秀";
      this.$data.comment = "";
      this.$refs.room.reset();
    },
    submit: function() {
      const result = this.$data.result;
      const comment = this.$data.comment;
      const build = this.$refs.room.$data.build;
      const door = this.$refs.room.$data.door;
      const singleRoom = this.$refs.room.$data.singleRoom;
      const room = this.$refs.room.$data.room;
      var that = this;
      Axios.post(this.$store.state.prefixUrl + "/teacher/add_HygieneHistory", {
        result: result,
        comment: comment,
        build: build,
        door: door,
        singleRoom: singleRoom,
        room: room
      })
        .then(function(data) {
          that.$message({
            message: "提交成功!",
            type: "success"
          });
        })
        .catch(function(error) {
          that.$message({
            message: error.response.data["status"]
              ? error.response.data["status"]
              : "网络错误, 请稍后再试!",
            type: "error"
          });
        });
    },
    upload_success: function() {
      this.$message({
        message: "上传成功!",
        type: "success"
      });
    },
    upload_error: function() {
      this.$message({
        message: "上传失败! 请检查文件内容并稍后尝试!",
        type: "error"
      });
    }
  }
};
</script>

<style scoped>
.input-div {
  display: inline-block;
  /* padding-left: 20%; */
  margin-top: 3vh;
  padding-right: 4%;
}

.input-div:nth-child(2) {
  padding-left: 5vw;
}

span {
  display: inline-block;
  width: 3em;
  margin-right: 1em;
}

.el-input {
  width: 42vw;
}

.center-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 2vh;
}

.mybutton {
  width: 3vw;
  height: 3vw;
}

.button-check {
  margin-left: 4vw;
}

.button-delete {
  margin-right: 4vw;
}

.item-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
}
</style>
