<template>
  <div id="body">
    <div class="InlineShow">
      <div class="block">
        <span class="demonstration">起始时间</span>
        <el-date-picker v-model="start_date" type="date" placeholder="选择日期"></el-date-picker>
      </div>
      <div class="block">
        <span class="demonstration">结束时间</span>
        <el-date-picker v-model="end_date" align="right" type="date" placeholder="选择日期"></el-date-picker>
      </div>
      <div class="block">
        <ChooseCampus ref="campus"></ChooseCampus>
      </div>
      <div class="block">
        <el-button @click="search" type="primary" v-loading.fullscreen.lock="isLoading">查询</el-button>
      </div>
    </div>
    <div class="table">
      <HygieneHistoryTable ref="table"></HygieneHistoryTable>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ChooseCampus from "../../plugin/ChooseCampus";
import HygieneHistoryTable from "../../plugin/HygieneHistoryTable";

export default {
  name: "HygieneHistory",
  components: {
    ChooseCampus,
    HygieneHistoryTable
  },
  data: function() {
    const date = new Date();
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    return {
      start_date: year + "-" + month + "-" + day,
      end_date: year + "-" + month + "-" + day,
      isLoading: false
    };
  },
  methods: {
    search: function() {
      this.$data.isLoading = true;
      var that = this;
      that.$refs.table.updateData(
        that.$data.start_date,
        that.$data.end_date,
        that.$refs.campus.$data.campus,
        that.$refs.campus.$data.detail,
        that
      );
    }
  }
};
</script>


<style scoped>
#body {
  width: 100%;
  height: 100%;
}

.InlineShow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

span {
  width: 4em;
}
.demonstration {
  margin-right: 2em;
  margin-left: 1em;
}

.block {
  margin: auto 1em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.table {
  margin: 1em auto;
}
</style>
