<template>
  <div class="MnageBed">
    <ChooseRoom ref="room"></ChooseRoom>

    <div class="input-div">
      <span>学号/教职工号</span>
      <el-input v-model="id" placeholder="请输入学号/教职工号"></el-input>
    </div>

    <div class="mybutton">
      <el-button @click="submit" type="success" round>确认</el-button>
    </div>

    <hr style="margin-top:10vh;color:gray;width:100%;margin-right:0;padding:0;" />

    <span
      style="  display: inline-block;
  width: 100%;
  text-align: center;
  font-size:2em;
  margin:0;"
    >通过文件导入</span>
    <el-upload
      class="upload-demo item-center"
      drag
      action="https://jsonplaceholder.typicode.com/posts/"
      accept=".xls, .xlsx"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
      </div>
      <div class="el-upload__tip" slot="tip">只能上传excel文件</div>
    </el-upload>
  </div>
</template>

<script>
/* eslint-disable */
import ChooseRoom from "../../plugin/ChooseRoom";
import Axios from "axios";
export default {
  name: "ManageBed",
  components: {
    ChooseRoom
  },
  data: function() {
    return {
      id: ""
    };
  },
  methods: {
    submit: function() {
      let that = this;
      const build = this.$refs.room.$data.build;
      const door = this.$refs.room.$data.door;
      const singleRoom = this.$refs.room.$data.singleRoom;
      const room = this.$refs.room.$data.room;
      const id = this.$data.id;
      Axios.post(this.$store.state.prefixUrl + "/teacher/ManageBed", {
        build: build,
        door: door,
        singleRoom: singleRoom,
        room: room,
        id: id
      })
        .then(function(error) {
          that.$message({
            message: "操作完成!",
            type: "success"
          });
        })
        .catch(function(error) {
          that.$message({
            message: error.response.data["status"]
              ? error.response.data["status"]
              : "网络错误, 请稍后重试!",
            type: "error"
          });
        });
    }
  }
};
</script>

<style scoped>
.input-div {
  display: inline-block;
  padding-left: 5vw;
  margin-top: 3vh;
  padding-right: 4%;
  width: 60%;
}

.el-input {
  width: 50%;
}

span {
  display: inline-block;
  width: 8em;
  margin-right: 1em;
}

.mybutton {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2vh;
}

.item-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2em;
}
</style>
