<template>
  <el-table
    class="mytable"
    :data="tableData"
    border
    style="width: 100%"
    height="70vh"
    :stripe="true"
    :highlight-current-row="true"
  >
    <el-table-column align="center" fixed prop="stu_id" label="学号" min-width="1"></el-table-column>
    <el-table-column align="center" prop="name" label="姓名" min-width="1"></el-table-column>
    <el-table-column align="center" prop="room_id" label="宿舍号" min-width="1"></el-table-column>
    <el-table-column align="center" prop="tel" label="电话" min-width="1"></el-table-column>
    <el-table-column align="center" prop="campus" label="学院" min-width="1"></el-table-column>
  </el-table>
</template>

<script>
/* eslint-disable */
import Axios from "axios";
export default {
  name: "SameRoomTable",
  data: function() {
    return {
      tableData: []
    };
  },
  mounted: function() {
    let that = this;
    const h = this.$createElement;
    Axios.post(this.$store.state.prefixUrl + "/student/same_room")
      .then(function(res) {
        that.tableData = res.data;
        that.$message({
          message: "信息获取成功!",
          type: "success"
        });
      })
      .catch(function(error) {
        that.$message({
          message: error.response.data["status"],
          type: "error"
        });
      });
  }
};
</script>

