<template>
  <el-table
    class="mytable"
    :data="tableData"
    border
    style="width: 100%"
    height="70vh"
    :stripe="true"
    :highlight-current-row="true"
  >
    <el-table-column align="center" prop="date" label="日期" min-width="1"></el-table-column>
    <el-table-column align="center" prop="result" label="结果" min-width="1"></el-table-column>
    <el-table-column align="center" prop="comment" label="备注" min-width="2"></el-table-column>
  </el-table>
</template>

<script>
/* eslint-disable */
import Axios from "axios";
export default {
  name: "MyHygieneHistory",
  data: function() {
    return {
      tableData: []
    };
  },
  mounted: function() {
    var that = this;
    Axios.post(this.$store.state.prefixUrl + "/student/result_of_check")
      .then(function(res) {
        that.tableData = res.data;
        that.$message({
          message: "获取信息成功!",
          type: "success"
        });
      })
      .catch(function(error) {
        that.$message({
          message: error.response.data["status"],
          type: "error"
        });
      });
  }
};
</script>

<style scoped>
.mytable {
  margin: auto;
}
</style>
