<template>
  <div class="choose-room">
    <div class="input-div">
      <span>宿舍楼</span>
      <el-select v-model="build" placeholder="请选择" @change="build_select">
        <el-option v-for="item in build_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div v-if="has_singleRoom" class="input-div">
      <span>门洞号</span>
      <el-select v-model="door" placeholder="请选择">
        <el-option v-for="item in door_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div v-if="has_singleRoom" class="input-div">
      <span>单间号</span>
      <el-select v-model="singleRoom" placeholder="请选择">
        <el-option v-for="item in singleRoom_options" :key="item" :label="item" :value="item"></el-option>
      </el-select>
    </div>

    <div class="input-div">
      <span>宿舍号</span>
      <el-input v-model="room" placeholder="请输入内容"></el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChooseRoom",
  data: function() {
    return {
      build: "1号公寓",
      door: "",
      singleRoom: "",
      room: "101",
      build_options: [
        "1号公寓",
        "2号公寓",
        "3号公寓",
        "4号公寓",
        "5号公寓",
        "6号公寓",
        "7号公寓"
      ],
      door_options: ["1门", "2门", "3门", "4门", "5门"],
      singleRoom_options: ["1", "2", "3", "4"],
      has_singleRoom: false
    };
  },
  methods: {
    build_select: function() {
      switch (this.$data.build) {
        case "1号公寓": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "2号公寓": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "3号公寓": {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
        case "4号公寓": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1门";
          this.$data.singleRoom = "1";
          break;
        }
        case "5号公寓": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1门";
          this.$data.singleRoom = "1";
          break;
        }
        case "6号公寓": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1门";
          this.$data.singleRoom = "1";
          break;
        }
        case "7号公寓": {
          this.$data.has_singleRoom = true;
          this.$data.door = "1门";
          this.$data.singleRoom = "1";
          break;
        }
        default: {
          this.$data.has_singleRoom = false;
          this.$data.door = "";
          this.$data.singleRoom = "";
          break;
        }
      }
    },
    reset: function() {
      this.$data.has_singleRoom = false;
      this.$data.build = "1号公寓";
      this.$data.door = "";
      this.$data.singleRoom = "";
      this.$data.room = "101";
    }
  }
};
</script>


<style scoped>
.choose-room {
  display: inline-block;
  width: 85%;
  margin: auto;
}

.input-div:last-child {
  margin-right: 5vw;
  padding: 0;
}

.choose-room:nth-child(1) {
  padding-left: 5vw;
}

.input-div {
  display: inline-block;
  padding-right: 5%;
  margin-top: 3vh;
}

span {
  display: inline-block;
  width: 3em;
  margin-right: 1em;
}

.el-input {
  width: 50%;
}
</style>
