<template>
  <el-table
    class="mytable"
    :data="tableData"
    border
    style="width: 100%"
    height="70vh"
    :stripe="true"
    :highlight-current-row="true"
  >
    <el-table-column align="center" fixed prop="sponsor" label="发送者" min-width="1"></el-table-column>
    <el-table-column align="center" prop="level" label="警告级别" min-width="1"></el-table-column>
    <el-table-column fixed="right" align="center" label="操作" min-width="1">
      <template slot-scope="scope">
        <el-button @click="see_comment(scope.row)" type="text" size="small">查看备注</el-button>
        <el-button @click="del(scope.row)" type="text" size="small">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import Axios from "axios";
/* eslint-disable */
export default {
  name: "WarningTable",
  data: function() {
    return {
      tableData: []
    };
  },
  mounted: function() {
    this.update();
  },
  methods: {
    see_comment: function(row) {
      const h = this.$createElement;
      const mes = row.comment;
      this.$notify({
        title: "备注信息",
        message: h("i", { style: "color: teal;white-space:normal;" }, mes)
      });
    },
    del: function(row) {
      const that = this;
      Axios.post(this.$store.state.prefixUrl + "/student/del_warning_message", {
        id: row.id
      })
        .then(function(res) {
          that.update();
        })
        .catch(function(err) {});
    },
    update: function() {
      var that = this;
      Axios.post(this.$store.state.prefixUrl + "/student/warning_message")
        .then(function(res) {
          that.tableData = res.data;
          that.$message({
            message: "信息获取成功!",
            type: "success"
          });
        })
        .catch(function(error) {
          that.$message({
            message: error.response.data["status"],
            type: "error"
          });
        });
    }
  }
};
</script>

