<template>
  <div class="AccountInformation">
    <div class="input-row">
      <span>姓名</span>
      <el-input placeholder="请输入内容" v-model="name" :disabled="true"></el-input>
    </div>

    <div class="input-row">
      <span>学院</span>
      <el-input placeholder="请输入内容" v-model="campus" :disabled="true"></el-input>
    </div>

    <div class="input-row">
      <span>电话号码</span>
      <el-input placeholder="请输入内容" v-model="tel" :disabled="true"></el-input>
    </div>

    <div class="input-row">
      <span>密码</span>
      <el-input
        placeholder="请输入密码"
        v-model="password"
        show-password
        v-loading.fullscreen.lock="isLoading"
      ></el-input>
    </div>

    <div class="input-row">
      <el-button type="primary" plain @click="update_info">修改密码</el-button>
    </div>

    <div class="input-row" style="margin-top:5vh">
      <svg viewBox="0 0 64 64" style="transform: scale(0.6, 0.6);">
        <g
          class="pin"
          stroke="#000"
          fill="#f32"
          stroke-width="1.5"
          stroke-linejoin="round"
          stroke-linecap="round"
        >
          <path
            class="pin-body"
            d="M16,20 q0,-16 16,-16 q16,0 16,16
           q0,8 -8,16 q-8,8-8,16
           q0,-8 -8,-16 q-8,-8 -8,-16"
          />

          <path d="M24,24 q0,8 8,8 q8,0 8,-8" />

          <circle cx="26" cy="16" r="3" fill="#fff" />
          <circle cx="26.5" cy="16.5" r=".5" fill="#000" />
          <circle cx="38" cy="16" r="3" fill="#fff" />
          <circle cx="37.5" cy="16.5" r=".5" fill="#000" />
        </g>
      </svg>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import Axios from "axios";
export default {
  name: "AccounttInformationManager",
  data: function() {
    return {
      password: "",
      isLoading: false,
      name: "",
      campus: "",
      tel: ""
    };
  },
  mounted: function() {
    let that = this;
    this.isLoading = true;

    Axios.post(this.$store.state.prefixUrl + "/student/account_information")
      .then(function(res) {
        that.isLoading = false;
        that.name = res.data["name"];
        that.campus = res.data["campus"];
        that.tel = res.data["tel"];
      })
      .catch(function(err) {
        that.isLoading = false;
        that.$message({
          message: err.response.data["status"],
          type: "error"
        });
      });
  },
  methods: {
    update_info: function() {
      if (this.password.trim().length <= 0) {
        return false;
      }
      let that = this;
      this.isLoading = true;
      Axios.post(this.$store.state.prefixUrl + "/student/update_account", {
        password: this.password
      })
        .then(function(res) {
          that.isLoading = false;
          that.$message({
            message: "更新信息成功!",
            type: "success"
          });
        })
        .catch(function(err) {
          that.isLoading = false;
          that.$message({
            message: err.response.data["status"],
            type: "error"
          });
        });
    }
  }
};
</script>

<style scoped>
.input-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 2vh;
}

span {
  width: 6em;
}

svg {
  width: 40vw;
  height: 40vh;
}

.pin {
  animation: floating 1.5s ease-in-out infinite;
}

.pin circle[fill="#000"] {
  animation: eye-anim 1.5s ease infinite;
}

.pin circle[fill="#000"]:last-of-type {
  animation-delay: -1s;
}

.pin-body {
  animation: pin-anim 1.5s linear infinite;
}

@keyframes pin-anim {
  0%,
  100% {
    d: path(
      "M16,20 q0,-16 16,-16 q16,0 16,16 q0, 8 -8, 16 q-8, 8 -8, 16 q0,-8 -8,-16 q-8,-8 -8,-16"
    );
  }
  50% {
    d: path(
      "M16,20 q0,-15 16,-16 q16,1 16,16 q0, 12 -8, 16 q-8, 4 -8, 16 q0,-12 -8,-16 q-8,-4 -8,-16"
    );
  }
}

@keyframes eye-anim {
  50% {
    transform: translateY(-1px);
  }
}

@keyframes floating {
  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(4px);
  }
}
</style>
